@import url(https://fonts.googleapis.com/css?family=Acme&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 12px;
}

.container {
    max-width: 1200px;
    margin: 0 auto; 
}


.nav-link {
    font-family: 'Merriweather', serif;
    font-size: 15px;
    padding-right: 1.5rem;
    padding-left: 1.5rem;
    text-transform: uppercase;
}

@media (min-width: 768px) {
    .navbar-expand-md .navbar-nav .nav-link {
        padding-right: 1.5rem;
        padding-left: 1.5rem;
    }
}

.d-inline-block {
margin: 0 5px;
}

.nav-bar {
    padding: 1rem 1rem;
}

.grow:hover{
    -webkit-transform: scale(1.05);
    transform: scale(1.05);
}

.about-wrapper {
    background: url(/portfolio/static/media/background.eb946522.jpg);
    background-size: cover;

}
.text-wrapper {
    text-align: center;

}
.about-heading {
    text-align: center;
    padding: 36px 0 5px 0;
    font-family: 'Acme', sans-serif;
    font-size: 30px;
    color: #000;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    margin: 0;
}

.hr-line { 
    border: none;
    height: 20px;
    width: 70%;
    height: 70px;
    margin-top: 0;
    box-shadow: 0 20px 20px -20px #333;
    margin: -50px auto 10px; 
}

.image-wrapper {
    flex: 1 1;
    text-align: center;
}

.about-image-wrapper {
    display: flex;
    max-width: 800px;
    margin: 10px auto 0 auto;
    
}

.about-item {
    font-size: 10px;
    font-family: 'Mitr', sans-serif;
    max-width: 640px;
    margin: auto;
}


@media only screen and (min-width: 600px) {
    .about-item {
        font-size: 15px;
    }
    .iw-image{
        width: 100%;
    }
}

.iw-image {
    width: 70%;
}
.image-text{
    font-size: 20px;
    font-family: 'Mitr', sans-serif;
    padding: 5px 0 ;
}

.am-image{
    border-radius: 50%;
    padding-bottom: 10px;
}

.card {
    background-color: transparent;
    border: none;
}

.card-header {
    text-align: center;
}

.card-design {
    padding: 10px;
    align-items: center;
}

.card-body {
    text-align: center;
}

.btn-link:hover, .btn-link:focus {
    color: #0056b3;
    text-decoration: none;
}

@media only screen and (max-width: 2400px) {
    .card-design {
        width: 24%;
    }

    .card-wrapper {
        display: flex;
        flex-wrap: wrap;
    }

    .card-img-top {
        width: 100%;
    }
}

@media only screen and (max-width: 600px) {
    .card-design {
        width: 100%;
    }

    .card-wrapper {
        display: block
    }

    .card-img-top {
        width: 60%;
    }

}

@media only screen and (max-width: 1024px)  and (min-width: 415px) and (orientation: portrait){
    .card-design {
        width: 50%;
    }

    .card-img-top {
        width: 60%;
    }
}
.banner-image {
    display: block;
    max-width: 100%;
    height: 50%;
    margin: 0 auto;
    width: 100%;
}

.banner-wrapper {
    background-image: url(/portfolio/static/media/banner2.a3971069.jpg);
    height: 430px;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.banner-intro {
    font-size: 60px;
    text-align: center;

}

.banner-name {
    font-family: 'Acme', sans-serif;
    font-size: 90px;
    padding-top: 40px;
    text-align: center;
    color:#d5d0d0;
}

@media only screen and (max-width: 768px) {
    .banner-intro {
        font-size: 30px;
    
    }
    .banner-name {
        font-size: 40px;

    }

    .banner-wrapper {
        height: 250px;

    }
    
}

.alt-nav-role, 
.alt-nav-name {
    font-family: 'Crimson Text', serif;
}   

.alt-nav-name {
    font-size: 25px;
   } 

.alt-nav-role {
    font-size: 18px;
}

.alt-nav-wrapper { 
    display: flex;
    background-color: aliceblue;

}
.alt-nav-details {
    width: 15%;
    margin-left: auto;
}

.alt-nav-icon-wrapper {
    width: 85%;
}

.alt-nav-icon-wrapper{
    position: absolute;
    right: 0;
    display: flex;
    padding: 12px;
    padding-left: 16px;
    width: 15%;
}

.alt-nav-item {
    width: 25%;
}

.alt-nav-item-icon {
    width: 75%;
}

@media only screen and (max-width: 1024px) {
    .alt-nav-icon-wrapper{
        width: 25%;
    }
    
}

@media only screen and (max-width: 415px) {
    .alt-nav-icon-wrapper{
        width: 40%;
    }
    
}
